import React from 'react'
import { Link } from 'react-router-dom'

const Video = () => {

    return (
        <>
            <div className="Header">
                <h3>Hostel Tour</h3>
                <div>
                    <Link to="/dashboard" id="backbtn">BACK</Link>
                </div>
            </div>
            <div className="VideoMain">
                <div className="Videos">
                    <div>
                        <h1>Boys Hostel</h1>
                        <iframe width="300" height="160" src="https://www.youtube.com/embed/UgLK7_7jWNc" title="YouTube video player" frameborder="0" allowfullscreen></iframe>
                    </div>
                    <br />
                    <div>
                        <h1>Girls Hostel</h1>
                        <iframe width="300" height="160" src="https://www.youtube.com/embed/77XfUZ8T5lQ" title="YouTube video player" frameborder="0" allowfullscreen></iframe>
                    </div>
                </div>
                <p>Disclaimer: Just for personal use only</p><br />
            </div>
        </>
    )
}

export default Video
